@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.movies {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 30px;
    padding-top: 70px;
    width: 100%;
  }
  
  .movie .movie__genres{
      list-style: none;
      display: -webkit-flex;
      display: flex;
      margin: 5px 0;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
  }
  .movie .movie__genres .genres__genre{
      display: block;
      margin-right: 16px;
      opacity: .6;
  }
  .movies .movie{
      display: -webkit-flex;
      display: flex;
      width: 500px;
      height: 300px;
      background-color: rgba(230,230,230,0.7);
      box-shadow:0px 6px 10px 0px rgba(170, 170, 170, 0.75);
      margin-bottom: 80px;
      /* border-radius: 5px; */
      /* flex-direction: column; */
      position: relative;
      padding: 15px 15px;
      transition: .2s ease-in-out;
  }
  .movies .movie:hover{
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  .movies .movie img{
      position: absolute;
      width: 140px;
      height: 210px;
      box-shadow:0px 6px 10px 0px rgb(170, 170, 170);
      top: -40px;
      left: 30px;
  }
  .movies .movie__data{
      /* position: absolute; */
      /* padding: 15px 15px 20px 15px; */
      padding-left: 170px;
      overflow-y: hidden;
  }
  .movie .movie__year{
      margin-top: 5px;
      opacity: .6;
  }
  .movie .movie__title{
      font-size: 25px;
      font-weight: 600;
  }
  .movie .movie__summary{
      display: block;
      margin-top: 5px;
      font-size: 15px;
  }
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
.container{
    width: 100%;
    height: 100%;
}
.loader {
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    
}
.loader .loader__text{
    font-size: 20px;
    font-weight: 500;
}

